
@media print
{
@page { margin: 0;
}
body { margin: 1.6cm;
}
.no-print
    {
        display: none !important;
}
.VueTables__search
    {
      display: none;
}
* {
      -webkit-print-color-adjust: exact;
}
}
table.VueTables__table {
border: 1px solid #1C6EA4;
background-color: #EEEEEE;
width: 100%;
text-align: left;
border-collapse: collapse;
}
table.VueTables__table td, table.VueTables__table th {
border: 1px solid #AAAAAA;
padding: 3px 2px;
}
table.VueTables__table tbody td {
font-size: 13px;
}
table.VueTables__table tr:nth-child(even) {
background: #D0E4F5;
}
table.VueTables__table thead {
background: #1C6EA4;
background: -webkit-gradient(linear, left top, left bottom, from(#5592bb), color-stop(66%, #327cad), to(#1C6EA4));
background: linear-gradient(to bottom, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
border-bottom: 2px solid #444444;
}
table.VueTables__table thead th {
font-size: 15px;
font-weight: bold;
color: #FFFFFF;
border-left: 2px solid #D0E4F5;
}
table.VueTables__table thead th:first-child {
border-left: none;
}
table.VueTables__table tfoot {
font-size: 14px;
font-weight: bold;
color: #FFFFFF;
background: #D0E4F5;
background: -webkit-gradient(linear, left top, left bottom, from(#dcebf7), color-stop(66%, #d4e6f6), to(#D0E4F5));
background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
border-top: 2px solid #444444;
}
table.VueTables__table tfoot td {
font-size: 14px;
}
table.VueTables__table tfoot .links {
text-align: right;
}
table.VueTables__table tfoot .links a{
display: inline-block;
background: #1C6EA4;
color: #FFFFFF;
padding: 2px 8px;
border-radius: 5px;
}



